.form-contaier {
    border-radius: 8px;
}

.sub-text {
    color: rgb(184, 184, 184);
}

li {list-style-type: none;}

.suggestionCard {
    background-color: white;
    position: absolute;
    z-index: 100;
    /* top: -1; */
    color: green;
    width: 100%;
    padding: 0;
    padding-bottom: 6px;
    border-left: solid 1px rgb(211, 211, 211);
    border-right: solid 1px rgb(211, 211, 211);
    border-bottom: solid 1px rgb(211, 211, 211);
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    max-height: 22rem;
    overflow-y: scroll;
}

.custom-drop {
    border-radius: 4px !important;
}

.custom-drop-none {
    border-bottom-left-radius: 0px !important;
    border-bottom-right-radius: 0px !important;
}

.suggestionCard::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  .suggestionCard::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  .suggestionCard::-webkit-scrollbar-thumb {
    background: #449D12; 
  }
  
  /* Handle on hover */
  .suggestionCard::-webkit-scrollbar-thumb:hover {
    background: #449D12; 
  }

.suggestionItem {
    padding: 2px 15px;
    cursor: pointer;
}

.suggestionItem:hover {
    background-color: rgba(49, 168, 85, 0.384);
}

/* form style */
.custom-drop {
    border: solid rgb(201, 201, 201) 1px;
    color: rgb(184, 184, 184);
    width: 100%;
}

.copy-btn:active {
    box-shadow:inset 0px 0px 85px rgba(241, 241, 241, 0.5) !important
}